import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import ListBanners from '../components/ListBanners.vue';
import MessageBox from '../components/MessageBox.vue';
import msgBoxTools from '../mixins/msgBoxTools';
import { bannersMapper } from '../module/store';
const Mappers = Vue.extend({
    computed: {
        ...bannersMapper.mapGetters(['listSorted'])
    },
    methods: {
        ...bannersMapper.mapActions(['getList'])
    }
});
let PageMain = class PageMain extends Mixins(msgBoxTools, Mappers) {
    constructor() {
        super(...arguments);
        this.msgBoxContent = {
            title: 'Сервер не отвечает',
            msg: 'Произошла ошибка при загрузке данных',
            loadBtn: 'Загрузить повторно'
        };
    }
    get list() { return this.listSorted; }
    async created() {
        if (!this.list || !this.list.length)
            this.loadData();
    }
    onClick() { this.$router.push({ path: '/banners/create' }); }
    async loadData() {
        this.msgBoxIsLoading = true;
        await this.getList()
            .then(() => console.log('List loaded!'))
            .catch(async (error) => {
            console.log(error);
            this.onErrorCatch();
        });
    }
};
PageMain = __decorate([
    Component({
        components: {
            ListBanners,
            MessageBox
        },
        mixins: [
            msgBoxTools
        ]
    })
], PageMain);
export default PageMain;
